ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

@font-face {
  font-family: "Bebas Neue";
  src: url("../fonts/BebasNeue-Regular.woff") format("woff"),
       url("../fonts/BebasNeue-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Bebas Neue";
  src: url("../fonts/BebasNeue-Bold.woff") format("woff"),
       url("../fonts/BebasNeue-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Roboto-Condensed.woff") format("woff"),
       url("../fonts/Roboto-Condensed.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Light.woff") format("woff"),
       url("../fonts/RobotoCondensed-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"),
       url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}


.container {
  width: 1366px;
  margin: 0 auto;
}

header {
  background: url("../img/header-bg.jpg") no-repeat;
  padding: 30px 103px;


  color: #FFFFFF;

  .header-top {
    display: flex;
    justify-content: space-between;
  }

  .header-main {
    display: flex;
    justify-content: flex-end;

    .header-main--info {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .header-image {
      margin: 0 185px 0 65px;
    }
  }


  .logo,
  .phone {
    font-family: Bebas Neue;
    font-size: 26px;
    line-height: 26px;
  }

  .city-name {
    font-family: Bebas Neue;
    font-size: 49px;
    line-height: 49px;
    text-align: right;
  }

  .fitness-name {
    font-family: Bebas Neue;
    font-size: 109px;
    line-height: 109px;
    font-weight: bold;
    text-align: right;

    margin-top: 25px;
    margin-bottom: 80px;
    position: relative;
  }

  .header-list {
    text-align: right;
    list-style: none;

    position: absolute;
    bottom: 35px;
    right: 245px;
    margin: 0;
    padding: 0;

    li {
      font-family: Roboto Condensed;
      font-weight: 300;
      font-size: 14px;
      line-height: 150%;
    }
  }

  .buy-button {
    text-align: center;
    a {
      display: inline-block;
      border: 4px solid #ffffff;
      width: 230px;
      height: 57px;
      background: transparent;
      font-family: Roboto Condensed;
      font-weight: bold;
      font-size: 12px;
      line-height: 14px;
      color: #ffffff;
      text-transform: uppercase;
      text-decoration: none;
      line-height: 58px;

      &:hover {
        cursor: pointer;
      }
    }
  }

  .social-links {
    font-family: Roboto Condensed;
    font-size: 12px;
    line-height: 14px;
    text-align: right;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin-bottom: 45px;
  }
  .social-link {
    margin-bottom: 25px;
  }
}
.gym {
  display: flex;
  justify-content: space-between;
  padding: 120px 103px;

  .gym-text {
    width: 355px;
    color: #102654;
  }

  .gym-text .note {
    font-family: Roboto Condensed;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    margin-bottom: 50px;
  }
  .gym-text p {
    font-family: "Roboto Condensed", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 150%;
  }
  .gym-image {
    position: relative;
    padding-bottom: 115px;
    padding-left: 65px;
  }
  .gym-image--gym2 {
    position: absolute;
    background-image: url("../img/play-button.png");
    bottom: 0;
    left: 0;
  }
}
.section-header {
  font-family: Bebas Neue;
  font-weight: 700;
  font-size: 49px;
  line-height: 49px;
  margin-bottom: 20px;
}

.subscriptions {
  text-align: center;
  background-color: #F8F9FC;
  padding: 120px 103px;
  position: relative;
  z-index: 1;

  .section-header {
    margin-top: 0;
  }
}

.tab-links {
  display: flex;
  justify-content: center;
  margin-bottom: 39px;

  .tab-link {
    border-bottom: 2px solid rgba(120, 120, 120, 0.2);
    font-family: "Roboto Condensed", sans-serif;
    font-size: 20px;
    line-height: 150%;
    color: rgba(16, 38, 84, 0.5);
    padding: 20px 23px 0;

    &:first-child {
      padding-left: 0;
    }
    &:last-child {
      padding-right: 0;
    }
    &.active {
      color: #102654;
      span {
        border-bottom: 2px solid #D10A42;
      }
    }
    span {
      padding-bottom: 20px;
      display: inline-block;
      margin-bottom: -2px;
      &:hover{
        cursor: pointer;
      }
    }
  }
}

.tab-content {
  justify-content: space-between;
  display: none;

  &.active {
    display: flex;
  }

  .option {
    width: 360px;
    background-color: #ffffff;
    text-align: center;
    padding: 68px 0;
    position: relative;
    border: 7px solid transparent;
    box-sizing: border-box;

    .option-name {
      font-family: "Roboto Condensed", sans-serif;
      font-weight: bold;
      font-size: 26px;
      line-height: 29px;
      color: #102654;
      margin-bottom: 10px;
      margin-top: 0;
    }

    .option-note {
      font-family: "Roboto Condensed", sans-serif;
      font-size: 16px;
      line-height: 150%;
      color: #495A7D;
      margin: 10px 0;
    }
    .option-price-bg {
      font-family: "Roboto Condensed", sans-serif;
      font-weight: bold;
      font-size: 100px;
      line-height: 150%;
      color: #E8E8E8;
      opacity: 0.48;
      margin: 10px 0 30px;
    }

    .option-price{
      font-family: "Roboto Condensed", sans-serif;
      font-weight: bold;
      font-size: 60px;
      line-height: 150%;
      color: #103AAE;
      position: absolute;
      width: 100%;
      top: 195px;
      margin: 0;
    }

    .ruble {
      color: #102654;
      font-weight: 400;
    }

    .buy-button {
      text-align: center;
      a {
        display: inline-block;
        border: 2px solid #102654;
        width: 206px;
        height: 64px;
        box-sizing: border-box;
        background: transparent;
        font-family: Roboto Condensed;
        font-weight: bold;
        font-size: 12px;
        color: #102654;
        text-transform: uppercase;
        text-decoration: none;
        line-height: 60px;

        &:hover {
          cursor: pointer;
        }
      }
    }

    &.active {
      position: relative;
      box-sizing: border-box;
      background-color: #fff;
      background-clip: padding-box;

      &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -1;
        margin: -7px;
        background: linear-gradient(135deg, #D10A42, #00359F);
      }

      .buy-button a {
        border: none;
        color: #fff;
        background: linear-gradient(to right, #D10A42, #00359F);
      }
    }
  }
}

.disk-image-wrapper img{
  position: absolute;
  right: 0;
  bottom: -150px;
  z-index: -1;
}
